/**
 * WordPress dependencies
 */
import { Button } from '@wordpress/components';
import { useDispatch, useSelect } from '@wordpress/data';
import { __, _x } from '@wordpress/i18n';
import { plus } from '@wordpress/icons';
import { PinnedItems } from '@wordpress/interface';

/**
 * Internal Dependencies
 */
import { STORE_NAME } from '../../store/constants';
import { MoreMenu } from '../header/more-menu';

export const MobileNav = () => {
	const { setIsInserterOpened } = useDispatch( STORE_NAME );
	/* istanbul ignore next */
	const { isInserterOpen } = useSelect( ( select ) => ( {
		isInserterOpen: select( STORE_NAME ).isInserterOpened(),
	} ), [] );

	return (
		<div className="edit-site-mobile-nav">
			<div className="edit-site-mobile-nav__item-slot nextgen-menu-icon nextgen-menu-icon--add">
				<Button
					isPrimary
					isPressed={ isInserterOpen }
					className="edit-site-header-toolbar__inserter-toggle nextgen-menu-icon__icon"
					onClick={ () =>
						setIsInserterOpened( ! isInserterOpen )
					}
					icon={ plus }
					label={ _x(
						'Add block',
						'Generic label for block inserter button'
					) }
				/>
				<span className="nextgen-menu-icon__label">
					{ __( 'Add', 'nextgen' ) }
				</span>
			</div>
			<PinnedItems.Slot scope={ STORE_NAME } className="edit-site-mobile-nav__items-slot" />
			<div className="edit-site-mobile-nav__item-slot">
				<MoreMenu />
			</div>
		</div>
	);
};
