/**
 * WordPress dependencies
 */
import '@wordpress/format-library';
import '@wordpress/editor';
import { render } from '@wordpress/element';
import { registerCoreBlocks } from '@wordpress/block-library';

/**
 * Internal dependencies
 */
import registerEditSiteStore from './store';
import Editor from './components/editor';
import deepLink from './components/deep-link';
import './style.scss';

/**
 * Initializes the site editor screen.
 *
 * @param {string} id ID of the root element to render the screen in.
 * @param {string} postType The initial post type.
 * @param {number} postId The initial post ID.
 * @param {Object} settings Editor settings.
 */
export function initializeRender( id, postType, postId, settings ) {
	initialize( settings );
	render(
		<Editor initialPostType={ postType } initialPostId={ postId } settings={ settings } />,
		document.getElementById( id )
	);
}

export function initialize( settings ) {
	registerEditSiteStore( { settings } );
	registerCoreBlocks();
	deepLink();
}

export { Editor as default };
export { default as MainDashboardButton } from './components/header/main-dashboard-button';
