/**
 * WordPress dependencies
 */
import { __, _x } from '@wordpress/i18n';
import { useDispatch, useSelect } from '@wordpress/data';
import { moreVertical } from '@wordpress/icons';
import { Button, DropdownMenu, MenuGroup, MenuItem } from '@wordpress/components';
import { ActionItem, ComplementaryArea } from '@wordpress/interface';

/**
 * Internal dependencies
 */
import { NextgenMenuIcon } from '@wpex/nextgen-common';

const POPOVER_PROPS = {
	className: 'edit-site-more-menu__content',
	position: 'bottom left',
};
const TOGGLE_PROPS = {
	tooltipPosition: 'bottom',
};

export const MoreMenu = () => {
	/* istanbul ignore next */
	const { isMobile } = useSelect( ( select ) => ( {
		isMobile: select( 'core/viewport' ).isViewportMatch( '< medium' ),
	} ), [] );

	return isMobile ? <MoreMenuMobileLayout /> : <MoreMenuDefaultLayout />;
};

export const MoreMenuContent = ( props ) => (
	<>
		<ActionItem.Slot
			name="core/edit-post/plugin-more-menu"
			label={ __( 'Plugins', 'nextgen' ) }
			as={ [ MenuGroup, MenuItem ] }
			fillProps={ { onClick: props.onClose } }
		/>
		<ActionItem.Slot
			name="nextgen/edit-site/plugin-more-menu"
			as={ [ MenuGroup, MenuItem ] }
			fillProps={ { onClick: props.onClose } }
		/>
	</>
);

const MoreMenuDefaultLayout = () => (
	<DropdownMenu
		className="edit-site-more-menu"
		icon={
			<NextgenMenuIcon
				icon={ moreVertical }
				label={ __( 'More', 'nextgen' ) }
				slug="site-more" />
		}
		label={ __( 'More tools & options', 'nextgen' ) }
		popoverProps={ POPOVER_PROPS }
		toggleProps={ TOGGLE_PROPS }
	>
		{ ( { onClose } ) => <MoreMenuContent onClose={ onClose } /> }
	</DropdownMenu>
);

const MoreMenuMobileLayout = () => {
	const { enableComplementaryArea } = useDispatch( 'core/interface' );

	const openMoreMenuComplementaryArea = () => enableComplementaryArea( 'nextgen/edit-site', 'nextgen-edit-site/edit-site-more' );

	return (
		<>
			<ComplementaryArea
				scope={ window.nextgen?.editSite ? 'nextgen/edit-site' : 'core/edit-post' }
				className="edit-site-mobile-nav__complementary"
				identifier="nextgen-edit-site/edit-site-more"
				title={ __( 'More', 'nextgen' ) }
				smallScreenTitle={ __( 'More', 'nextgen' ) }
				isPinnable={ false }>
				<MoreMenuContent />
			</ComplementaryArea>

			<Button
				onClick={ openMoreMenuComplementaryArea }
				icon={
					<NextgenMenuIcon
						icon={ moreVertical }
						label={ __( 'More', 'nextgen' ) }
						slug="site-more" />
				}
				label={ _x(
					'More tools & options',
					'Generic label for block inserter button'
				) }
			/>
		</>
	);
};
