/**
 * WordPress Dependencies
 */
import { useEffect } from '@wordpress/element';

/**
 * Internal Dependencies
 */
import { STORE_NAME } from '../../store/constants';
import { BLOCK_INSPECTOR_IDENTIFIER } from '../sidebar';
import { useDispatch, useSelect } from '@wordpress/data';

export default function useEditorBehavior() {
	const {
		openGeneralSidebar,
		closeGeneralSidebar,
		setIsInserterOpened,
	} = useDispatch( STORE_NAME );

	const {
		clearSelectedBlock,
	} = useDispatch( 'core/block-editor' );

	const {
		disableComplementaryArea,
	} = useDispatch( 'core/interface' );

	const { disablePublishSidebar } = useDispatch( 'core/editor' );

	/* istanbul ignore next */
	const {
		sidebarIsOpened,
		selectedNextGenSidebar,
		selectedEditPostSidebar,
		isInserterOpened,
		hasSelectedBlock,
		isPublishSidebarEnabled,
	} = useSelect( ( select ) => ( {
		sidebarIsOpened: select( 'core/interface' ).getActiveComplementaryArea( STORE_NAME ) || select( 'core/interface' ).getActiveComplementaryArea( 'core/edit-post' ),
		selectedNextGenSidebar: select( 'core/interface' ).getActiveComplementaryArea( STORE_NAME ),
		selectedEditPostSidebar: select( 'core/interface' ).getActiveComplementaryArea( 'core/edit-post' ),
		isInserterOpened: select( STORE_NAME ).isInserterOpened(),
		hasSelectedBlock: select( 'core/block-editor' ).hasSelectedBlock(),
		isPublishSidebarEnabled: select( 'core/editor' ).isPublishSidebarEnabled(),
	} ), [] );

	useEffect( () => {
		if ( sidebarIsOpened ) {
			setIsInserterOpened( false );
			if ( sidebarIsOpened !== BLOCK_INSPECTOR_IDENTIFIER ) {
				clearSelectedBlock();
			}
		}
	}, [ sidebarIsOpened ] );

	useEffect( () => {
		if ( isInserterOpened ) {
			closeGeneralSidebar();
		}
	}, [ isInserterOpened ] );

	// Handle disable of pre-publish sidebar
	useEffect( () => {
		if ( isPublishSidebarEnabled ) {
			disablePublishSidebar();
		}
	}, [ isPublishSidebarEnabled ] );

	// Switch to the block inspector automatically if any sidebar is already open.
	useEffect( () => {
		if ( hasSelectedBlock && sidebarIsOpened && sidebarIsOpened !== BLOCK_INSPECTOR_IDENTIFIER ) {
			openGeneralSidebar( BLOCK_INSPECTOR_IDENTIFIER );
		}
	}, [ hasSelectedBlock ] );

	// Hide nextgen/edit-site sidebars if a core/edit-post sidebar is open and vice versa.
	useEffect( () => {
		if ( !! selectedEditPostSidebar ) {
			closeGeneralSidebar();
		}
	}, [ selectedEditPostSidebar ] );

	useEffect( () => {
		if ( !! selectedNextGenSidebar ) {
			disableComplementaryArea( 'core/edit-post' );
		}
	}, [ selectedNextGenSidebar ] );
}
