/**
 * WordPress dependencies
 */
import { getQueryArg } from '@wordpress/url';
import { dispatch } from '@wordpress/data';

/**
 * Internal dependencies
 */
import {
	PLUGIN_NAME as SITE_DESIGN_PLUGIN_NAME,
	SIDEBAR_NAME as SITE_DESIGN_SIDEBAR_NAME,
} from '@wpex/nextgen-site-design/src/constant.js';

import {
	PLUGIN_NAME as SITE_CONTENT_PLUGIN_NAME,
	SIDEBAR_NAME as SITE_CONTENT_SIDEBAR_NAME,
} from '@wpex/nextgen-site-content/src/constant.js';

export default function deepLink() {
	const focusArg = getQueryArg( window.location.href, 'focus' );

	if ( ! focusArg ) {
		return;
	}

	const { openGeneralSidebar } = dispatch( 'nextgen/edit-site' );

	/**
	 * Keeping the repetition here since we have different method to open panel and sidebars.
	 */
	switch ( focusArg ) {
		case 'site-design': {
			openGeneralSidebar( `${ SITE_DESIGN_PLUGIN_NAME }/${ SITE_DESIGN_SIDEBAR_NAME }` );
			break;
		}
		case 'site-content': {
			openGeneralSidebar( `${ SITE_CONTENT_PLUGIN_NAME }/${ SITE_CONTENT_SIDEBAR_NAME }` );
			break;
		}
	}
}
