/**
 * WordPress dependencies
 */
import { Button } from '@wordpress/components';
import { __, sprintf } from '@wordpress/i18n';
import { useDispatch, useSelect } from '@wordpress/data';

/**
 * Internal dependencies
 */
import { STORE_NAME } from '../../../store/constants';
import {
	DOCUMENT_SETTINGS_IDENTIFIER,
	BLOCK_INSPECTOR_IDENTIFIER,
} from '../';

const SettingsHeader = ( { sidebarName } ) => {
	const { enableComplementaryArea } = useDispatch( 'core/interface' );

	const openDocumentSettings = () => enableComplementaryArea( STORE_NAME, DOCUMENT_SETTINGS_IDENTIFIER );
	const openBlockSettings = () => enableComplementaryArea( STORE_NAME, BLOCK_INSPECTOR_IDENTIFIER );

	/* istanbul ignore next */
	const documentLabel = useSelect( ( select ) => {
		const currentPostType = select( 'core/editor' ).getCurrentPostType();
		const postType = select( 'core' ).getPostType( currentPostType );

		return (
			// Disable reason: Post type labels object is shaped like this.
			// eslint-disable-next-line camelcase
			postType?.labels?.singular_name ??
				// translators: Default label for the Document sidebar tab, not selected.
				__( 'Document', 'nextgen' )
		);
	} );

	const [ documentAriaLabel, documentActiveClass ] = sidebarName === DOCUMENT_SETTINGS_IDENTIFIER
		? [ sprintf(
			// translators: ARIA label for the Document sidebar tab, selected. %s Document label.
			__( '%s (selected)', 'nextgen' ),
			documentLabel
		), 'is-active' ]
		: [ documentLabel, '' ];

	const [ blockAriaLabel, blockActiveClass ] = sidebarName === BLOCK_INSPECTOR_IDENTIFIER
		// translators: ARIA label for the Block Settings Sidebar tab, selected.
		? [ __( 'Block (selected)', 'nextgen' ), 'is-active' ]
		// translators: ARIA label for the Block Settings Sidebar tab, not selected.
		: [ __( 'Block', 'nextgen' ), '' ];

	/* Use a list so screen readers will announce how many tabs there are. */
	return (
		<ul>
			<li>
				<Button
					onClick={ openDocumentSettings }
					className={ `edit-post-sidebar__panel-tab ${ documentActiveClass }` }
					aria-label={ documentAriaLabel }
					data-label={ documentLabel }
				>
					{ documentLabel }
				</Button>
			</li>
			<li>
				<Button
					onClick={ openBlockSettings }
					className={ `edit-post-sidebar__panel-tab ${ blockActiveClass }` }
					aria-label={ blockAriaLabel }
					// translators: Data label for the Block Settings Sidebar tab.
					data-label={ __( 'Block', 'nextgen' ) }
				>
					{
						// translators: Text label for the Block Settings Sidebar tab.
						__( 'Block', 'nextgen' )
					}
				</Button>
			</li>
		</ul>
	);
};

export default SettingsHeader;
