/**
 * WordPress Dependencies
 */
import { controls } from '@wordpress/data';

/**
 * Internal Dependencies
 */
import { STORE_NAME } from './constants';

/**
 * Returns an action object used in signalling that the user opened an editor sidebar.
 *
 * @param {?string} name Sidebar name to be opened.
 *
 * @yield {Object} Action object.
 */
export function* openGeneralSidebar( name ) {
	yield controls.dispatch(
		'core/interface',
		'enableComplementaryArea',
		STORE_NAME,
		name
	);
}

/**
 * Returns an action object signalling that the user closed the sidebar.
 *
 * @yield {Object} Action object.
 */
export function* closeGeneralSidebar() {
	yield controls.dispatch(
		'core/interface',
		'disableComplementaryArea',
		STORE_NAME
	);
}

/**
 * Returns an action object used to toggle a feature flag.
 *
 * @param {string} feature Feature name.
 *
 * @return {Object} Action object.
 */
export function toggleFeature( feature ) {
	return {
		type: 'TOGGLE_FEATURE',
		feature,
	};
}

/**
 * Returns an action object used to set whether the block inserter panel should be open.
 *
 * @param {boolean} isOpen If true, opens the inserter. If false, closes it. It does not toggle the state, but sets it directly.
 *
 * @return {Object} Action object.
 */
export function setIsInserterOpened( isOpen ) {
	return {
		type: 'SET_IS_INSERTER_OPENED',
		isOpen,
	};
}

/**
 * Returns an action object used to update the settings.
 *
 * @param {Object} settings New settings.
 *
 * @return {Object} Action object.
 */
export function updateSettings( settings ) {
	return {
		type: 'UPDATE_SETTINGS',
		settings,
	};
}

/**
 * Set the device preview.
 *
 * @param {string} deviceType Desktop, Mobile, Tablet
 */
export function setPreviewDeviceType( deviceType ) {
	return {
		type: 'SET_PREVIEW_DEVICE_TYPE',
		deviceType,
	};
}
