/**
 * WordPress dependencies
 */
import { createSlotFill } from '@wordpress/components';
import { useSelect } from '@wordpress/data';
import { __ } from '@wordpress/i18n';
import { cog } from '@wordpress/icons';

/**
 * Internal dependencies
 */
import { NextgenMenuIcon } from '@wpex/nextgen-common';
import DefaultSidebar from './default-sidebar';
import { STORE_NAME } from '../../store/constants';
import SettingsHeader from './settings-header';
import PostStatus from './post-status';
import LastRevision from './last-revision';
import PostLink from './post-link';
import PostTaxonomies from './post-taxonomies';
import FeaturedImage from './featured-image';
import PostExcerpt from './post-excerpt';

const { Slot: InspectorSlot, Fill: InspectorFill } = createSlotFill(
	'EditSiteSidebarInspector'
);
export const SidebarInspectorFill = InspectorFill;

export const DOCUMENT_SETTINGS_IDENTIFIER = 'edit-site/document';
export const BLOCK_INSPECTOR_IDENTIFIER = 'edit-site/block-inspector';

/**
 * A sidebar combining the Document Settings and Block Inspector sidebars.
 */
export function SidebarComplementaryAreaFills() {
	const { sidebarName } = useSelect( ( select ) => {
		let activeSidebar = select( 'core/interface' ).getActiveComplementaryArea( STORE_NAME );
		// Default to the Document Settings sidebar when opened.
		if ( ! [ DOCUMENT_SETTINGS_IDENTIFIER, BLOCK_INSPECTOR_IDENTIFIER ].includes( activeSidebar ) ) {
			activeSidebar = DOCUMENT_SETTINGS_IDENTIFIER;
		}
		return { sidebarName: activeSidebar };
	}, [] );

	return (
		<>
			<DefaultSidebar
				className="edit-post-sidebar"
				identifier={ sidebarName }
				title={ __( 'Settings', 'nextgen' ) }
				smallScreenTitle={ __( 'Settings', 'nextgen' ) }
				icon={
					<NextgenMenuIcon
						icon={ cog }
						label={ __( 'Settings', 'nextgen' ) }
						slug="site-settings" />
				}
				closeLabel={ __( 'Close settings', 'nextgen' ) }
				headerClassName="edit-post-sidebar__panel-tabs"
				header={ <SettingsHeader sidebarName={ sidebarName } /> }
				isActiveByDefault
			>
				{ sidebarName === DOCUMENT_SETTINGS_IDENTIFIER && (
					<>
						<PostStatus />
						<LastRevision />
						<PostLink />
						<PostTaxonomies />
						<FeaturedImage />
						<PostExcerpt />
					</>
				) }
				{ sidebarName === BLOCK_INSPECTOR_IDENTIFIER && <InspectorSlot bubblesVirtually /> }
			</DefaultSidebar>
		</>
	);
}
