/**
 * WordPress dependencies
 */
import { _x } from '@wordpress/i18n';
import { Button, Icon } from '@wordpress/components';
import {
	PinnedItems,
} from '@wordpress/interface';
import {
	EditorHistoryUndo,
	EditorHistoryRedo,
	PostPublishButton,
	PostPreviewButton,
} from '@wordpress/editor';
import {
	BlockNavigationDropdown,
	__experimentalPreviewOptions as PreviewOptions,
} from '@wordpress/block-editor';
import { useSelect, useDispatch } from '@wordpress/data';
import { plus, external } from '@wordpress/icons';

/**
 * Internal Dependencies.
 */
import { MoreMenu } from './more-menu';
import { STORE_NAME } from '../../store/constants';
import MainDashboardButton from './main-dashboard-button';

export const Header = ( { setEntitiesSavedStatesCallback } ) => {
	/* istanbul ignore next */
	const { isMobile } = useSelect( ( select ) => ( {
		isMobile: select( 'core/viewport' ).isViewportMatch( '< medium' ),
	} ) );

	return (
		isMobile
			? <HeaderMobileLayout type="mobile" setEntitiesSavedStatesCallback={ setEntitiesSavedStatesCallback } />
			: <HeaderDefaultLayout type="default" setEntitiesSavedStatesCallback={ setEntitiesSavedStatesCallback } />
	);
};

const HeaderDefaultLayout = ( props ) => {
	const { setIsInserterOpened, setPreviewDeviceType } = useDispatch( STORE_NAME );
	/* istanbul ignore next */
	const { devicePreviewType, isInserterOpen } = useSelect( ( select ) => ( {
		devicePreviewType: select( STORE_NAME ).getDevicePreviewType(),
		isInserterOpen: select( STORE_NAME ).isInserterOpened(),
	} ), [] );

	return (
		<div className={ `edit-site-header edit-site-header--${ props.type }` }>
			<div className="edit-site-header_start">
				<HeaderMainDashboardButton />

				<div className="edit-site-header__toolbar">
					<Button
						isPrimary
						isPressed={ isInserterOpen }
						className="edit-site-header-toolbar__inserter-toggle"
						onClick={ () =>
							setIsInserterOpened( ! isInserterOpen )
						}
						icon={ plus }
						label={ _x(
							'Add block',
							'Generic label for block inserter button'
						) }
					/>

					<EditorHistoryUndo />
					<EditorHistoryRedo />
					<BlockNavigationDropdown />
				</div>
			</div>

			<div className="edit-site-header_end">
				<div className="edit-site-header__actions">
					<PreviewOptions
						deviceType={ devicePreviewType }
						setDeviceType={ setPreviewDeviceType }
					>
						<div className="edit-site-header-preview-container">
							<HeaderPostPreviewButton />
						</div>
					</PreviewOptions>

					<PinnedItems.Slot scope={ STORE_NAME } />
					<PinnedItems.Slot scope="core/edit-post" />
					<PostPublishButton
						setEntitiesSavedStatesCallback={ props.setEntitiesSavedStatesCallback }
					/>
					<MoreMenu />
				</div>
			</div>
		</div>
	);
};

const HeaderMobileLayout = ( props ) => {
	return (
		<div className={ `edit-site-header edit-site-header--${ props.type }` }>
			<div className="edit-site-header_start">
				<HeaderMainDashboardButton />
			</div>

			<div className="edit-site-header_end">
				<div className="edit-site-header__actions">
					<PinnedItems.Slot scope={ STORE_NAME } />
					<HeaderPostPreviewButton />
					<PostPublishButton
						setEntitiesSavedStatesCallback={ props.setEntitiesSavedStatesCallback }
					/>
				</div>
			</div>
		</div>
	);
};

export const HeaderMainDashboardButton = () => (
	<MainDashboardButton.Slot>
		<Button>MainDashboardButton</Button>
	</MainDashboardButton.Slot>
);

export const HeaderPostPreviewButton = () => (
	<PostPreviewButton
		className="edit-post-header-preview__button-external"
		role="menuitem"
		textContent={ (
			<>
				<span className="edit-post-header-preview__button-external__text">
					{ _x( 'Preview in new tab' ) }
				</span>
				<Icon icon={ external } />
			</>
		) }
	/>
);
