/**
 * External dependencies
 */
import { get } from 'lodash';

/**
 * Returns whether the given feature is enabled or not.
 *
 * @param {Object} state Global application state.
 * @param {string} feature Feature slug.
 *
 * @return {boolean} Is active.
 */
export const isFeatureActive = ( state, feature ) => get( state.preferences.features, [ feature ], false );

/**
 * Returns the settings.
 *
 * @param {Object} state Global application state.
 *
 * @return {Object} Settings.
 */
export const getSettings = ( state ) => state.settings;

/**
 * Returns the current edited post type.
 *
 * @param {Object} state Global application state.
 *
 * @return {string} Post type.
 */
export const getEditedPostType = ( state ) => state.editedPost.type;

/**
 * Returns the current edited post id.
 *
 * @param {Object} state Global application state.
 *
 * @return {number} Post ID.
 */
export const getEditedPostId = ( state ) => state.editedPost.id;

/**
 * Returns the current page object.
 *
 * @param {Object} state Global application state.
 *
 * @return {string} Page.
 */
export const getPage = ( state ) => state.editedPost.page;

/**
 * Returns the current opened/closed state of the inserter panel.
 *
 * @param {Object} state Global application state.
 *
 * @return {boolean} True if the inserter panel should be open; false if closed.
 */
export const isInserterOpened = ( state ) => state.blockInserterPanel;

/**
 * Returns the current device preview type: Desktop, Mobile, Tablet.
 *
 * @param {Object} state Global application state.
 *
 * @return {string} The current device being previewed.
 */
export const getDevicePreviewType = ( state ) => {
	return state.deviceType;
};
