/**
 * WordPress dependencies.
 */
import { useState, useCallback } from '@wordpress/element';
import { useSelect, useDispatch } from '@wordpress/data';
import {
	Button,
	DropZoneProvider,
	FocusReturnProvider,
	KeyboardShortcuts,
	Popover,
	SlotFillProvider,
} from '@wordpress/components';
import { __experimentalLibrary as Library } from '@wordpress/block-editor';
import {
	EditorProvider,
	AutosaveMonitor,
	LocalAutosaveMonitor,
	UnsavedChangesWarning,
	EditorKeyboardShortcutsRegister,
} from '@wordpress/editor';
import {
	ComplementaryArea,
	FullscreenMode,
	InterfaceSkeleton,
} from '@wordpress/interface';
import { PluginArea } from '@wordpress/plugins';
import { close } from '@wordpress/icons';
import { __ } from '@wordpress/i18n';

/**
 * Internal dependencies
 */
import BlockEditor from '../block-editor';
import { Header } from '../header';
import { MobileNav } from '../mobile-nav';
import { SidebarComplementaryAreaFills } from '../sidebar';
import { STORE_NAME } from '../../store/constants';
// eslint-disable-next-line import/no-unresolved
import BrowserURL from 'edit-post/components/browser-url';
import useEditorBehavior from '../hooks/useEditorBehavior';
import ActionsPanel from './actions-panel';

const interfaceLabels = {
	leftSidebar: __( 'Block library' ),
	/* translators: accessibility text for the editor top bar landmark region. */
	header: __( 'Editor top bar' ),
	/* translators: accessibility text for the editor content landmark region. */
	body: __( 'Editor content' ),
	/* translators: accessibility text for the editor settings landmark region. */
	sidebar: __( 'Editor settings' ),
	/* translators: accessibility text for the editor publish landmark region. */
	actions: __( 'Editor publish' ),
	/* translators: accessibility text for the editor footer landmark region. */
	footer: __( 'Editor footer' ),
};

function Editor( { initialPostType, initialPostId, settings } ) {
	useEditorBehavior();

	const { setIsInserterOpened } = useDispatch( STORE_NAME );

	const {
		sidebarIsOpened,
		isInserterOpen,
		isMobile,
		postObject,
	} = useSelect( ( select ) => {
		const {
			isInserterOpened,
			getSettings,
		} = select( STORE_NAME );

		const currentPostObj = select( 'core/editor' ).getCurrentPost();

		const { getEntityRecord } = select( 'core' );

		return {
			sidebarIsOpened: select( 'core/interface' ).getActiveComplementaryArea( STORE_NAME ) || select( 'core/interface' ).getActiveComplementaryArea( 'core/edit-post' ),
			isInserterOpen: isInserterOpened(),
			isMobile: select( 'core/viewport' ).isViewportMatch( '< medium' ),
			settings: getSettings(),
			postObject: currentPostObj?.id ? currentPostObj : getEntityRecord( 'postType', initialPostType, initialPostId ),
		};
	}, [] );

	const [
		entitiesSavedStatesCallback,
		setEntitiesSavedStatesCallback,
	] = useState( false );
	const closeEntitiesSavedStates = useCallback(
		( arg ) => {
			if ( typeof entitiesSavedStatesCallback === 'function' ) {
				entitiesSavedStatesCallback( arg );
			}
			setEntitiesSavedStatesCallback( false );
		},
		[ entitiesSavedStatesCallback ]
	);

	if ( ! postObject ) {
		return null;
	}

	return (
		<>
			<FullscreenMode isActive={ true } />
			{ /* Temporary disable for heisenberg */ }
			{ ! document.location.href.includes( 'godaddy.com' ) && <BrowserURL /> }
			<UnsavedChangesWarning />
			<AutosaveMonitor />
			<LocalAutosaveMonitor />
			<EditorKeyboardShortcutsRegister />
			<SlotFillProvider>
				<DropZoneProvider>
					<EditorProvider
						settings={ settings }
						post={ postObject }
						initialEdits={ [] }
						useSubRegistry={ false }
					>
						<FocusReturnProvider>
							<SidebarComplementaryAreaFills />
							<Popover.Slot name="block-toolbar" />
							<InterfaceSkeleton
								className={ 'edit-post-layout' }
								labels={ interfaceLabels }
								header={
									<Header
										setEntitiesSavedStatesCallback={ setEntitiesSavedStatesCallback }
									/>
								}
								content={
									<BlockEditor />
								}
								footer={
									isMobile ? <MobileNav /> : null
								}
								secondarySidebar={ isInserterOpen && (
									<div className="edit-site-editor__inserter-panel">
										<div className="edit-site-editor__inserter-panel-header">
											<Button icon={ close } onClick={ () => setIsInserterOpened( false ) } />
										</div>
										<div className="edit-site-editor__inserter-panel-content">
											<Library onSelect={ () => isMobile ? setIsInserterOpened( false ) : null } />
										</div>
									</div>
								) }
								sidebar={ sidebarIsOpened && (
									<>
										<ComplementaryArea.Slot scope={ STORE_NAME } />
										<ComplementaryArea.Slot scope="core/edit-post" />
									</>
								) }
								actions={
									<ActionsPanel
										closeEntitiesSavedStates={ closeEntitiesSavedStates }
										isEntitiesSavedStatesOpen={ entitiesSavedStatesCallback }
										setEntitiesSavedStatesCallback={ setEntitiesSavedStatesCallback }
									/>
								}
							/>
							<Popover.Slot />
							<PluginArea />
						</FocusReturnProvider>
						<KeyboardShortcuts />
					</EditorProvider>
				</DropZoneProvider>
			</SlotFillProvider>
		</>
	);
}

export default Editor;
