/**
 * External dependencies
 */
import mergeRefs from 'react-merge-refs';

/**
 * WordPress dependencies
 */
import {
	PostTitle,
	VisualEditorGlobalKeyboardShortcuts,
} from '@wordpress/editor';
import {
	WritingFlow,
	BlockList,
	__unstableUseBlockSelectionClearer as useBlockSelectionClearer,
	__unstableUseTypewriter as useTypewriter,
	__unstableUseClipboardHandler as useClipboardHandler,
	__unstableUseTypingObserver as useTypingObserver,
	__experimentalUseResizeCanvas as useResizeCanvas,
	__unstableUseEditorStyles as useEditorStyles,
} from '@wordpress/block-editor';
import { Popover } from '@wordpress/components';
import { useRef, useCallback } from '@wordpress/element';

/**
 * Internal dependencies
 */
import { useSelect } from '@wordpress/data';
import { STORE_NAME } from '../../store/constants';

function VisualEditor() {
	const ref = useRef();

	const { settings, deviceType } = useSelect( ( select ) => ( {
		settings: select( STORE_NAME ).getSettings(),
		deviceType: select( STORE_NAME ).getDevicePreviewType(),
	} ), [] );

	const desktopCanvasStyles = {
		height: '100%',
		paddingBottom: '40vh',
	};
	const resizedCanvasStyles = useResizeCanvas( deviceType );

	const getResizedCanvasStyles = () => {
		if ( ! resizedCanvasStyles ) {
			return null;
		}

		return {
			...resizedCanvasStyles,
			margin: '24px auto',
			top: '50%',
			position: 'relative',
			transform: 'translateY( calc( -50% - 24px ) )',
		};
	};

	useBlockSelectionClearer( ref );
	useTypewriter( ref );
	useClipboardHandler( ref );
	useTypingObserver( ref );

	const editorStylesRef = useEditorStyles( settings.styles );
	const mergedRefs = useCallback( mergeRefs( [ ref, editorStylesRef ] ), [
		ref,
		editorStylesRef,
	] );

	return (
		<div className="edit-post-visual-editor">
			<VisualEditorGlobalKeyboardShortcuts />
			<Popover.Slot name="block-toolbar" />
			<div
				ref={ mergedRefs }
				className="editor-styles-wrapper"
				style={ getResizedCanvasStyles() || desktopCanvasStyles }
			>
				<WritingFlow>
					<div className="edit-post-visual-editor__post-title-wrapper">
						<PostTitle />
					</div>
					<BlockList />
				</WritingFlow>
			</div>
		</div>
	);
}

export default VisualEditor;
